nav {
    display: flex;
    justify-content: end;
    padding: 10px;

    background-color: #3BABAD;
    position: sticky;
    z-index: 5;
    top: 0;

}
.navlinks {
    padding: 20px;
    color: white;
    text-decoration: none;
    font-weight: 700;
    cursor: pointer;
}

.hover-underline-animation{
    display: inline-block;
    position: relative;

}

.hover-underline-animation:after {
    content: '';
    position: absolute;
    width: 80%;
    transform: scaleX(0);
    height: 2px;
    bottom: 8px;
    left: 10px;
    background-color: white;
    transform-origin: bottom center;
    transition: transform 0.25s ease-out;
  }

  .hover-underline-animation:hover:after {
    transform: scaleX(1);
    transform-origin: bottom center;
  }

  #isActiveHome{
    color: #346a6c;

  }

  #isActiveProjects{
    color: #346a6c;
  }

  #isActiveContact{
    color: #346a6c;
  }